'use client'
import { useRouter } from "next/router";
import { useState, useEffect } from "react";

export default function MyApplication() {
  const router = useRouter();
  const [internship, setInternship] = useState(null);

  useEffect(() => {
    if (router.isReady && Object.keys(router.query).length > 0) {
      setInternship({
        title: router.query.title || "N/A",
        company: router.query.company || "N/A",
        location: router.query.location || "N/A",
        start_date: router.query.start_date || "N/A",
        duration: router.query.duration || "N/A",
        stipend: router.query.stipend || "N/A",
      });
    }
  }, [router.isReady, router.query]);

  if (!internship) {
    return <p className="text-center">Loading...</p>;
  }

  return (
    <div className="max-w-2xl mx-auto mt-10 p-6 bg-white shadow-lg rounded-lg">
      <h1 className="text-3xl font-bold text-yellow-600 mb-4">{internship.title}</h1>
      <p><strong>Company:</strong> {internship.company}</p>
      <p><strong>Location:</strong> {internship.location}</p>
      <p><strong>Start Date:</strong> {internship.start_date}</p>
      <p><strong>Duration:</strong> {internship.duration}</p>
      <p><strong>Stipend:</strong> {internship.stipend}</p>

      <hr className="my-4" />

      {/* Upload Resume Form */}
      <form className="mt-4">
        <label className="block mb-2 font-semibold">Upload Your Resume (PDF Only):</label>
        <input type="file" accept=".pdf" className="border border-gray-300 p-2 rounded w-full" />

        <button type="submit" className="mt-4 w-full bg-yellow-600 text-white font-semibold py-2 px-4 
                     rounded-lg hover:bg-yellow-700 transition duration-300">
          Submit Application
        </button>
      </form>
    </div>
  );
}
